export const processFunction31 = (property) => {
  const results31 = [];
  if (property) {
    const binaryArray = property.split('').map((hexDigit) => {
        const binaryDigit = parseInt(hexDigit, 16).toString(2).padStart(4, '0');
        return binaryDigit;
    });
    const binaryString = binaryArray.join('');
    for (let i = 399; i >= 0; i--) {
        const bitValue = binaryString[i];
        const buttonName = `${400 - i}`;
        const result = { property: buttonName, value: bitValue === '0' ? '' : '' };
        results31.push(result);
    }
    const deniedButtons = results31.filter((result) => result.value === '');
    const propertyString = deniedButtons.map(result => result.property).join(',');
    if (results31.every((result) => result.value === '')) {
        return [{ property: '', value: '' }];
    } else if (results31.every((result) => result.value === '')) {
        return [{ property: '', value: '' }];
    }
    return [{ property: <b></b>, value: propertyString }];
  } else {
    return [{ property: '', value: '' }];
  }
};
