import React from 'react';

export const LtSpecificTable0 = ({fileName, folder, volume, mixing, output, external, channel, hour, minute }) => {
  const rowHeaders = ["", "", "", "", ""];

  return (
    <table align='center'>
      <tbody>
      <tr textalign='center'>
        <th width='30%' colSpan={1}></th>
        <td colSpan={4}><b>{hour}:{minute}</b></td>
      </tr>
      <tr textalign='center'>
        <th width='30%'></th>
        <td colSpan={4}><b></b></td>
      </tr>
      <tr>
        <td ></td>
        <th width='30%'></th>
        <th width='20%'></th>
        <th width='10%'></th>
        <th width='10%'></th>
      </tr>
      {rowHeaders.map((header, index) => (
        <tr key={index}>
          <th>{header}</th>
          <td>{fileName[index]}</td>
          <td>{folder[index]}</td>
          <td>{volume[index]}</td>
          <td>{mixing[index]}%</td>
        </tr>
      ))}
      <tr>
          <th colSpan={1}></th>
          <td></td>
          <td>{output[0]}</td>
          <td>{output[1]}</td>
          <td>{output[2]}</td>
      </tr>
      <tr>
          <th colSpan={1}></th>
          <td colSpan={4}>{external[0]}</td>
      </tr>
      {external[0] !== "" && (
        <>
          <tr>
            <th width='30%'></th>
            <td width='30%'>{external[1]}</td>
            <td colSpan={3}>(1~16)</td>
          </tr>
          <tr>
            <th width='30%'></th>
            <td colSpan={4}>{external[2]}</td>
          </tr>
          {external[2] === "" && (
          <tr>
            <th width='30%'></th>
            <td width='30%' colSpan={3}>{external[3]}</td>
            <td>(1~99)</td>
          </tr>
          )}
        </>
      )}
      <tr>
        <th width='30%'>ch</th>
        <td colSpan={4}>{channel[0]}</td>
      </tr>
      {channel[0] !== "" && (
        <>
          <tr>
            <th width='30%'></th>
            <td colSpan={4}>{channel[1]}</td>
          </tr>
        </>
      )}
      </tbody>
    </table>
  )
};

export const LtSpecificTable1 = ({ power, hour, minute}) => {
    return (
      <table align='center'>
        <tbody>
        <tr textalign='center'>
            <th width='30%'></th>
            <td ><b>{hour}:{minute}</b></td>
        </tr>
        <tr textalign='center'>
          <th width='30%'></th>
          <td><b>ON/OFF</b></td>
        </tr>
        <tr>
          <th width='30%'></th>
          <td>{power}</td>
        </tr>
        </tbody>
      </table>
    )
};

