import React from 'react';

export const ScDetailTable0 = ({fileName, folder, volume, mixing, output, external, channel, back }) => {
  const rowHeaders = ["", "", "", "", ""];

  return (
    <table align='center'>
      <tbody>
      <tr textalign='center'>
        <td colSpan={5}><b></b></td>
      </tr>
      <tr>
        <td width='20%'></td>
        <th width='30%'></th>
        <th width='30%'></th>
        <th width='10%'></th>
        <th width='10%'></th>
      </tr>
      {rowHeaders.map((header, index) => (
        <tr key={index}>
          <th>{header}</th>
          <td>{fileName[index]}</td>
          <td>{folder[index]}</td>
          <td>{volume[index]}</td>
          <td>{mixing[index]}%</td>
        </tr>
      ))}
      <tr>
          <th colSpan={1}></th>
          <td></td>
          <td>{output[0]}</td>
          <td>{output[1]}</td>
          <td>{output[2]}</td>
      </tr>
      <tr>
          <th colSpan={1}></th>
          <td colSpan={4}>{external[0]}</td>
      </tr>
      {external[0] !== "" && (
        <>
          <tr>
            <th width='30px'></th>
            <td width='30px'>{external[1]}</td>
            <td colSpan={3}>(1~16)</td>
          </tr>
          <tr>
            <th width='30px'></th>
            <td colSpan={4}>{external[2]}</td>
          </tr>
          {external[2] === "" && (
          <tr>
            <th width='30px'></th>
            <td width='30px' colSpan={3}>{external[3]}</td>
            <td>(1~99)</td>
          </tr>
          )}
        </>
      )}
      <tr>
        <th>ch</th>
        <td colSpan={4}>{channel[0]}</td>
      </tr>
      {channel[0] !== "" && (
        <>
          <tr>
            <th width='30px'></th>
            <td colSpan={4}>{channel[1]}</td>
          </tr>
        </>
      )}
      </tbody>
    </table>
  )
};

